import React, { useEffect } from 'react'
import { useState } from 'react';

/* ################################################### */
/*          Style                                   */
/* ################################################### */
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import Divider from '@mui/material/Divider';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import Avatar from '@mui/material/Avatar';
import StarIcon from "@mui/icons-material/Star";
import SettingsIcon from '@mui/icons-material/Settings';


import './App.css';

const styles = {
  Indicator: {
      justifyContent: 'space-around'
  }
};

function App() {

  const [items, setItems] = useState([]);
  const [pageNo, addPageNo] = useState(1);
  const [errMsg, setErrorMsg] = useState('');
  const [title, setTitle] = useState('Trending Repos');
  const [tab, setTab] = useState(0);

  useEffect(() => {
    getListing();
    addPageNo(0)
    setTab(0);
  }, []);

  const tabChange = (event, newValue) => {
    setTab(newValue);
  };


 const getListing = async (pageNo) => {
  console.log('App getListing pageNo =>', pageNo);
  let url = 'https://api.github.com/search/repositories?q=created:%3e2024-07-15&sort=stars&order=desc'
  if (pageNo) url = `${url}${'&page='}${pageNo}`

  fetch(url)
    .then((response) => response.json())
    .then((data) => { 
      if(data.items){
        setItems(items => [...items, ...data.items])
      } else {
        setTitle('Authentication error, please refresh after few minutes');
        setErrorMsg(data.message);
      }
      
    })
    .catch((err) => {
      console.log(err.message);
      setTitle('Authentication error, please refresh after few minutes');
      setErrorMsg(err.message);
    });


 }


  return (
    <div>
        <Card variant="outlined">
          <Box height={15} p={4} display="flex" alignItems="center">
            <Stack direction="row" justifyContent="space-between" alignItems="center">
              <Typography gutterBottom variant="h5" component="div">
                {title}
              </Typography>
            </Stack>
          </Box>
          <Divider />
          <Box
            onScroll={(event) => {
              let targetElement = event.target
              if (
                targetElement.clientHeight -
                  (targetElement.scrollHeight - targetElement.scrollTop) >=
                -20
              ) {
                addPageNo(pageNo+1);
                getListing(pageNo);
              }
            }}
            style={{
              overflowY: 'auto',
              flexGrow: 1,
              flexDirection: 'column',
              maxHeight: '870px'
            }}
          >
            {
              items.length && !errMsg ?
              items.map(
                (item, index) => (
                  <Card variant="outlined" key={`trending-list-${index}`}>
                    <CardHeader
                      title={item.name}
                    />
                    <CardContent>
                      <Typography variant="body2" color="text.secondary">
                        {item.description}
                      </Typography>
                    </CardContent>
                    <CardActions sx={{ display: 'flex'}}>
                        <Avatar alt={item.name} src={item.owner.avatar_url} />
                        <Typography variant="body2" color="text.secondary">
                          {item.owner.login}
                        </Typography>
                      
                        <IconButton style={{marginLeft: 'auto'}} aria-label="comments">
                          <StarIcon />
                        </IconButton>
                        <Typography variant="body2" color="text.secondary">
                          {item.stargazers_count}
                        </Typography>
                    </CardActions>
                  </Card>
                )
              ) : (
                <Card>
                  <CardContent>
                      <Typography variant="body2" color="text.secondary">
                        {errMsg}
                      </Typography>
                    </CardContent>
                </Card>
              )
            }
          </Box>
          <Box sx={{ width: '100%' }}>
            <Tabs value={tab} onChange={tabChange} aria-label="icon tabs example" indicatorColor={'#FFFFFF'} variant='fullWidth'>
              <Tab icon={<StarIcon />} aria-label="trending" label="Trending"  sx={{textTransform: 'none'}}/>
              <Tab icon={<SettingsIcon />} aria-label="settings" label="Settings"  sx={{textTransform: 'none'}}/>
            </Tabs>
          </Box>
        </Card>
    </div>
  );
}

export default App;
